a {
  text-decoration: none;
  color: inherit;
}

ul {
  padding: 0;
  list-style-type: none;
}

.menu {
  display: flex;
  gap: 10px;
}

.menu>li {
  position: relative;
}

.menu>li:hover>ul {
  visibility: visible;
}

.menu ul {
  position: absolute;
  list-style-type: none;
  z-index: 1;
  background-color: wheat;
  padding: 10px;
  box-shadow: 0 1px 5px 0 #0004;
  visibility: hidden;
}